.main-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #184653;
  z-index: 100;
}

.bar {
  padding: 5px 0px;
  margin: auto;
  max-width: 1100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list {
  list-style: none;
  display: flex;
}

.list li {
  margin-right: 15px;
}

.list li a {
  font-weight: bold;
  padding-right: 10px;
  letter-spacing: 0.8px;
  text-decoration: none;
  color: #d89770;
}

.bar .right {
  font-size: 2rem;
  color: #d89770;
  padding-left: 1rem;
}

.left {
  display: flex;
  align-items: center;
}

.menu-icon {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  cursor: pointer;
}

.menu-icon.open .bar1 {
  transform: rotate(-45deg) translate(-6px, 4px);
}

.menu-icon.open .bar2 {
  opacity: 0;
}

.menu-icon.open .bar3 {
  transform: rotate(45deg) translate(-6px, -4px);
}

.bar1,
.bar2,
.bar3 {
  width: 100%;
  height: 3px;
  background-color: #333;
  transition: all 0.3s;
}

@media (max-width: 768px) {
  .menu-icon {
    display: flex;
  }

  .list {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 15px;
  }

  .list.open {
    display: flex;
  }

  .right {
    font-size: 1.5rem;
  }
}
